import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/shared/services/auth.service';
import * as moment from 'moment';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit {
  
  loginForm: FormGroup;
  registerForm: FormGroup;
  enableRegister = false;
  loginWrongCredentials = false;
  emailExists = false;
  hideLoginPassword = true;
  hideRegisterPassword = true;
  loader = false;

  // *** Constructor ***
  constructor(
    private fb: FormBuilder,
    private _authService: AuthService,
    private _router: Router
    ) {

    // Prepare Login Form
    this.loginForm = this.fb.group({
      email: [null, [Validators.required, Validators.email]],
      password: [null, [Validators.required, Validators.minLength(4)]]
    });

    // Prepare Register Form
    this.registerForm = this.fb.group({
      email: [null, [Validators.required, Validators.email]],
      name: [null, [Validators.required]],
      phoneNumber: [null, [Validators.required]],
      password: [null, [Validators.required, Validators.minLength(4)]],
      registrationDate: [null, [Validators.required]],
    });
  }
  
  // *** Life-cycle Hooks ***
  ngOnInit(): void {

    // check if token present - temporary fix. Needs to be added in auth component
    console.log(this._authService.getToken());
    if(this._authService.getToken()) {
      this._router.navigate(['/main/dashboard']);
    } else {
      this._router.navigate(['/login']);
    }
  }


  // *** Login Action ***
  login(): any {
    
    // Return if form is invalid
    if(this.loginForm.invalid) {
      return;
    }

    // Enable Loader
    this.loader = true;

    console.log(this.loginForm.value);

    // Disable form untill response or error is recieved
    this.loginForm.disable();

    // Submit User Credentials - Check and Authenticate
    this._authService.loginUser(this.loginForm.value).subscribe((response: any) => {
      console.log(response);

      // Store the token
      localStorage.setItem('token', response.token);

      // Store the user details
      localStorage.setItem('userName', response.user.name);

      // Set to false if wrong credentials
      this.loginWrongCredentials = false;

      // disable Loader
      this.loader = false;

      // Navigate to dashboard
      this._router.navigate(['main/dashboard']);

    }, (error: any) => {
      console.log(error);

      // Enable the form
      this.loginForm.enable();

      // Set to true if wrong credentials
      this.loginWrongCredentials = true;

      // disable Loader
      this.loader = false;
    });
  }


  // *** Register Action ***
  register(): any {
    console.log(this.registerForm);
    // Set Registration date
    this.registerForm.get('registrationDate')?.setValue(moment().format('MMMM Do YYYY, h:mm:ss a'));
    
    // Return if form is invalid
    if(this.registerForm.invalid) {
      return;
    }

    console.log(this.registerForm.value);

    // set to false before posting credentials details
    this.emailExists = false;

    // Disable form untill response or error is recieved
    this.registerForm.disable();

    // Post User Credentials - Store and Authenticate
    this._authService.registerUser(this.registerForm.value).subscribe((response: any) => {
      console.log(response);

      // If email does not exist, then store the token and navigate
      if(response) {

        // If email exists, set this to false
        this.emailExists = false;

        // Store the token
        localStorage.setItem('token', response.token);

        // Enable the form
        this.registerForm.enable();

        // Navigate to Dashboard
        this._router.navigate(['main']);
      } else {

        // If email exists, set this to true
        this.emailExists = true;

        // Enable the form
        this.registerForm.enable();
      }
      
    }, (error: any) => {
      console.log(error);

      // Enable the form
      this.registerForm.enable();
    });
  }

}
