import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/shared/services/auth.service';
import { SideMenuService } from 'src/app/shared/services/side-menu.service';
import { ConfirmationDialogComponent } from './confirmation/confirmation.component';
import { sideMenuItems } from './main-side-menu';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {

  showFiller = false;
  sideMenuList = sideMenuItems;
  todaysDate = new Date();
  currentMainRoute = null;
  userLoggedInName: any;
  userDetails: any;

  constructor(
    private _sideMenuService: SideMenuService,
    private _router: Router,
    private _authService: AuthService,
    public _dialog: MatDialog,
  ) { }

  ngOnInit(): void {

    // *** Check for User Name ***
    // if(this._authService.getLoggedInUserDetails()) {
    //   this.userLoggedInName = this._authService.getLoggedInUserDetails();
    // } else {
    //   this._authService.loggedOut();
    // }
    // console.log(this._authService.getLoggedInUserDetails());
    
    // *** Check for token and userdetails ***
    this._authService.getUserDetails().subscribe((data: any) => {
      console.log(data);
      this.userDetails = data.user;
    }, error => {
      if( error instanceof HttpErrorResponse) {
        if(error.status === 401) {
          this._router.navigate(['/login']);
        }
      }
    });
    
    // *** Side Menu Highlighter ***
    this._sideMenuService.route.subscribe((routeValue) => {
      this.currentMainRoute = routeValue;
      console.log(this.currentMainRoute);
    })
  }


  // *** Side menu navigation ***
  sideMenuNavigation(route: string): any {
    if(route === '/main/dashboard/user-profile') {
      this._router.navigate([route, this.userDetails._id]);
    } else {
      this._router.navigate([route]);
    }
    
  }


   // Open Logout Dialog Box
   openDialog(): void {
    let dialogRef = this._dialog.open(ConfirmationDialogComponent, {
      width: 'max-w-md'
    });
  
    dialogRef.afterClosed().subscribe((result: any) => {
      console.log(result);

      if(result) {
        this.logout();
      }
    });
  }


  // *** Logout ***
  logout(): any {
    this._authService.loggedOut();
  }


  // *** User Profile Navigate ***
  navigate(): any {
    this._router.navigate(['main/dashboard/user-profile', this.userDetails._id]);
  }

}
